<template>
  <div class="flex-box crd" style="height: 100%">
    <el-card style="width: 32rem; margin-top: 1rem">
      <div class="flex-box">
        <div class="box" @click="updateImg">
          <img :src="userInfo.img" class="image" />
          <div class="zhe">
            <div>
              <i
                style="font-size: 1.4rem"
                class="el-icon-picture-outline-round"
              ></i>
            </div>
            <div style="margin-top: 0.5rem">更换头像</div>
          </div>
        </div>
      </div>

      <div style="padding: 14px 0">
        <div class="text-title">用户信息修改</div>
        <div class="bottom clearfix">
          <el-form
            :model="userInfo"
            :rules="rules"
            ref="ruleForm"
            label-width="100px"
            class="demo-ruleForm"
          >
            <el-form-item label="附加信息" prop="info">
              <el-input
                v-model="userInfo.info"
                placeholder="附加信息"
              ></el-input>
            </el-form-item>
            <el-form-item label="用户昵称" prop="nickname">
              <el-input
                v-model="userInfo.nickname"
                placeholder="用户昵称"
              ></el-input>
            </el-form-item>
            <el-form-item label="用户QQ" prop="qq">
              <el-input placeholder="用户qq" v-model="userInfo.qq"></el-input>
            </el-form-item>
            <el-form-item label="用户微信" prop="wechat">
              <el-input
                v-model="userInfo.wechat"
                placeholder="用户微信"
              ></el-input>
            </el-form-item>
            <el-form-item label="性别" prop="sex">
              <el-radio-group v-model="userInfo.sex">
                <el-radio label="m">男</el-radio>
                <el-radio label="f">女</el-radio>
                <el-radio label="n">保密</el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="updateUserInfo"
                >确定修改</el-button
              >
              <el-button @click="resetForm">重置</el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
import tools from '@/js/tools';
export default {
  data() {
    let valiateQQ = (rule, value, callback) => {
      let qq = /^[1-9][0-9]{4,10}$/;
      if (qq.test(value)) {
        callback();
      } else {
        callback(new Error('此qq不满足规范'));
      }
    };
    return {
      userImg: '',
      file: '',
      userInfo: {
        info: '',
        nickname: '',
        qq: '',
        sex: '',
        wechat: '',
        img: ''
      },
      user: {},
      rules: {
        info: [
          { required: true, message: '请输入用户详细信息', trigger: 'blur' }
        ],
        nickname: [
          { required: true, message: '请输入用户昵称', trigger: 'blur' }
        ],
        wechat: [
          { required: true, message: '请输入用户微信', trigger: 'blur' }
        ],
        sex: [{ required: true, message: '必须选择性别', trigger: 'change' }],
        qq: [
          { required: true, message: '请输入用户QQ', trigger: 'blur' },
          { validator: valiateQQ, trigger: 'blur' }
        ]
      }
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert('submit!');
        } else {
          return false;
        }
      });
    },
    resetForm() {
      this.userInfo = JSON.parse(JSON.stringify(this.user));
    },
    updateUserInfo() {
      tools.ajax('/user/auth/updateUserInfo', this.userInfo, (data) => {
        this.$message(data.message);
        if (this.userImg != this.userInfo.img) {

          if (
            this.userImg.includes(
              'https://huhuiyu.top/teach_project_service/user/file/download?fid='
            )
          ) {
            let num = Number(this.userImg.slice(65));
            tools.ajax(
              '/user/file/delete',
              { fid: num },
              (data) => {
              },
              true
            );
          }
        }

        this.$store.dispatch('updatedUser');
      });
    },
    updateImg() {
      let input = document.createElement('input');
      input.setAttribute('type', 'file');
      input.setAttribute('accept', 'image/*');
      input.addEventListener('change', (event) => {
        if (event.target.files[0].size <= 1024 * 2 * 1024) {
          this.file = event.target.files[0];
          this.$message('上传中。。。时间较长请耐心等待');
          tools.upload(
            '/user/file/upload',
            { fileinfo: '图像修改' },
            this.file,
            (data) => {
              this.$message({
                message: '上传成功需要保存修改图片才能被保存修改',
                type: 'warning'
              });
              this.userInfo.img =
                tools.serverInfo.serverUrl +
                '/user/file/download' +
                '?fid=' +
                data.data.fid;
            }
          );
        } else {
          this.$message({
            message: '文件超过2MB请选择重新上传',
            type: 'error',
            showClose: true
          });
          return;
        }
      });
      318249;
      input.click();
    }
  },
  created() {
    this.user = this.$store.state.user.tbUserInfo;
    this.user.nickname = this.$store.state.user.tbUser.nickname;
    delete this.user.lastupdate;
    delete this.user.phone;
    delete this.user.email;
    this.userInfo = JSON.parse(JSON.stringify(this.user));
    this.userImg = this.userInfo.img;
  }
};
</script>

<style scoped>
img {
  height: 100%;
  width: 100%;
  position: absolute;
  border: 0px solid #fff;
  border-radius: 15%;
}
.text-title {
  font-size: 1.7rem;
  margin-bottom: 2rem;
}
.demo-ruleForm {
  margin-right: 1rem;
}
.box {
  position: relative;
  width: 11rem;
  height: 11rem;
  border: 0px solid #fff;
  border-radius: 15%;
}
.zhe {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.3);
  width: 11rem;
  height: 11rem;
  left: 0;
  top: 0;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-size: 1.2rem;
  border: 0px solid #fff;
  border-radius: 15%;
}
</style>
